/**
 * 
 */
export default class BarcodeScanner {
  constructor() {
    if (window.x_scanner || window.wx) {
      console.log('barcodescanner is ready')
    } else {
      return Object.create(null)
    }
  }

  /**
   * QRCode
   * @param {function} successCallback 
   * @param {function} failureCallback failureCallback
   */
  scanQRCode(successCallback, failureCallback) {
    if (window.x_scanner)
      window.x_scanner.scanQRCode(successCallback, failureCallback)
    else if (window.wx) {
      /**
       * 
       *
       * @param {Number} needResult 01
       * @param {String[]} scanType ['qrCode', 'barCode'],
       * @param {function(Object)} successCallback needResult  1 {resultStr: ''}
       * @param {function(Object)} errorCallback  {"errMsg":""}
       * @param {Function} completeCallback 
       * @memberof WX
       */
      window.wx.scanQRCode({
        needResult: 1,
        scanType: ['qrCode'],
        success: successCallback,
        fail: failureCallback,
        complete: () => {}
      })
    }
  }

  /**
   * qrcode
   * @param {function(String)} successCallback ,base64
   * @param {function} failureCallback 
   * @param {String} qrCode 
   * @param {Number} width 
   *
   */
  encodeQRCode(successCallback, failureCallback, qrCode, width) {
    if (window.x_scanner)
      window.x_scanner.encodeQRCode(
        successCallback,
        failureCallback,
        qrCode,
        width
      )
    else if (window.wx) {
      console.error('qrcode')
      failureCallback()
    }
  }
}



