export default class System {
  constructor() {
    // console.log('SystemPluginReady')

    if (window.x_system || window.wx) {
      console.log('SystemPluginReady')
    } else {
      return Object.create(null)
    }
  }
  /**
   * 
   *
   * @param {function(string)} successCallback 
   *  (string) {"cid":"xxxx","imei":"","ver":""}
   * @param {Function} errorCallback 
   * @memberof System
   */
  getDeviceInfo(successCallback, errorCallback) {
    if (window.x_system)
      window.x_system.getDeviceInfo(successCallback, errorCallback)
    else if (window.wx) {
      console.error('')
      errorCallback()
    }
  }
  /**
   * 
   *
   * @param {function(string)} successCallback 
   *  (string)  "1.0"  "3.4b5".
   * @param {Function} errorCallback 
   * @memberof System
   */
  getOSVersion(successCallback, errorCallback) {
    if (window.x_system)
      window.x_system.getOSVersion(successCallback, errorCallback)
    else if (window.wx) {
      console.error('')
      errorCallback()
    }
  }
  /**
   * .
   *
   * @param {function(number)} successCallback 
   *  0:, 1:2:WIFI
   * @param {Function} errorCallback 
   * @memberof System
   */
  getNetworkType(successCallback, errorCallback) {
    if (window.x_system)
      window.x_system.getNetworkType(successCallback, errorCallback)
    else if (window.wx) {
      window.wx.getNetworkType({
        success: res => {
          if (
            res.networkType === '2g' ||
            res.networkType === '3g' ||
            res.networkType === '4g'
          ) {
            successCallback(1)
          } else if (res.networkType === 'wifi') {
            successCallback(2)
          } else {
            successCallback(0)
          }
        },
        fail: errorCallback,
        complete: () => {}
      })
    }
  }
  /**
   * PushCID
   *
   * @param {function(number)} successCallback 
   *  pushcid
   * @param {Function} errorCallback 
   * @memberof System
   */
  getPushCID(successCallback, errorCallback) {
    if (window.x_system)
      window.x_system.getPushCID(successCallback, errorCallback)
    else if (window.wx) {
      console.error('PushCID')
      errorCallback()
    }
  }
  /**
   * 
   *
   * @param {Function} successCallback 
   * @param {Function} errorCallback 
   * @memberof System
   */
  cleanCache(successCallback, errorCallback) {
    if (window.x_system)
      window.x_system.cleanCache(successCallback, errorCallback)
    else if (window.wx) {
      console.error('')
      errorCallback()
    }
  }
  /**
   * 
   *
   * @param {number} maxSelec 
   * @param {number} width 
   * @param {number} height 
   * @param {function(string)} successCallback 
   *   "["file://xxx/xx.png","file://xxx/xxx.png"]"
   * @param {Function} errorCallback 
   * @memberof System
   */
  photoPicker(maxSelec, width, height, successCallback, errorCallback) {
    if (window.x_system)
      window.x_system.photoPicker(
        successCallback,
        errorCallback,
        maxSelec,
        width,
        height
      )
    else if (window.wx) {
      /**
       * 
       *
       * @param {Number} count 9
       * @param {String[]} sizeType ,['original', 'compressed']
       * @param {String[]} sourceType , ['album', 'camera']
       * @param {function(WXLocalFile)} successCallback {localIds[]},localIdsIDlocalIdimgsrc
       * @param {function(Object)} errorCallback  {"errMsg":""}
       * @param {Function} completeCallback 
       * @memberof WX
       */
      window.wx.chooseImage({
        count: maxSelec,
        sizeType: ['original', 'compressed'],
        sourceType: ['album', 'camera'],
        success: res => {
          successCallback(res.localIds)
        },
        fail: errorCallback,
        complete: () => {}
      })
    }
  }
  /**
   * 
   *
   * @param {string} selectcode 
   * @param {function(JSON)} successCallback 
   *   (  {"pname":"","cname":"","aname":"","acode":""})
   * @param {Function} errorCallback 
   * @memberof System
   */
  addressPicker(selectcode, successCallback, errorCallback) {
    if (window.x_system)
      window.x_system.addressPicker(successCallback, errorCallback, selectcode)
    else if (window.wx) {
      console.error('')
      errorCallback()
    }
  }
  /**
   * 
   *
   * @param {Function} successCallback 
   * @param {Function} errorCallback 
   * @memberof System
   */
  improveBrightness(successCallback, errorCallback) {
    if (window.x_system)
      window.x_system.improveBrightness(successCallback, errorCallback)
    else if (window.wx) {
      console.error('')
      errorCallback()
    }
  }
  /**
   * 
   *
   * @param {Function} successCallback 
   * @param {Function} errorCallback 
   * @memberof System
   */
  resetBrightness(successCallback, errorCallback) {
    if (window.x_system)
      window.x_system.resetBrightness(successCallback, errorCallback)
    else if (window.wx) {
      console.error('')
      errorCallback()
    }
  }
  /**
   * 
   *
   * @param {boolean} auto 
   * @param {Function} successCallback 
   * @param {Function} errorCallback 
   * @memberof System
   */
  setAutoBrightness(auto, successCallback, errorCallback) {
    if (window.x_system)
      window.x_system.setAutoBrightness(successCallback, errorCallback, auto)
    else if (window.wx) {
      console.error('')
      errorCallback()
    }
  }
  /**
   * 
   *
   * @param {number} brightness  (0-255)
   * @param {Function} successCallback 
   * @param {Function} errorCallback 
   * @memberof System
   */
  setWindowsBrightness(brightness, successCallback, errorCallback) {
    if (window.x_system)
      window.x_system.setWindowsBrightness(
        successCallback,
        errorCallback,
        brightness
      )
    else if (window.wx) {
      console.error('')
      errorCallback()
    }
  }
  /**
   * 
   *
   * @param {function(number)} successCallback 
   *   (0-255)
   * @param {Function} errorCallback 
   * @memberof System
   */
  getWindowsBrightness(successCallback, errorCallback) {
    if (window.x_system)
      window.x_system.getWindowsBrightness(successCallback, errorCallback)
    else if (window.wx) {
      console.error('')
      errorCallback()
    }
  }
  /**
   * 
   *
   * @param {Function} successCallback 
   * @param {Function} errorCallback 
   * @memberof System
   */
  isAutoBrightness(successCallback, errorCallback) {
    if (window.x_system)
      window.x_system.isAutoBrightness(successCallback, errorCallback)
    else if (window.wx) {
      console.error('')
      errorCallback()
    }
  }
  /**
   * Push
   *
   * @param {string} messageId messageid
   * @param {Function} callback push
   * @param {Function} successCallback 
   * @param {Function} errorCallback 
   * @memberof System
   */
  registerPushListener(messageId, callback, successCallback, errorCallback) {
    if (window.x_system)
      window.x_system.registerPushListener(
        successCallback,
        errorCallback,
        messageId,
        callback
      )
    else if (window.wx) {
      console.error('Push')
      errorCallback()
    }
  }

  /**
   * Push
   *
   * @param {string} messageId  messageid
   * @param {Function} successCallback 
   * @param {Function} errorCallback 
   * @memberof System
   */
  unRegisterPushListener(messageId, successCallback, errorCallback) {
    if (window.x_system)
      window.x_system.unRegisterPushListener(
        successCallback,
        errorCallback,
        messageId
      )
    else if (window.wx) {
      console.error('Push')
      errorCallback()
    }
  }

  /**
   * @typedef {Object} OssFile
   * @description 
   * @property {string} filePath '/storage/emulated/0/Boohee/20170628nYafd3YTpW.png'
   * @property {string} ossPath OSS'jhx/user/image/wrq_zj/20170628nYafd3YTpW.png'
   */
  /**
   * @typedef {Object} FileReq
   * @description 
   * @property {string} bucket OSSid'xb-image'
   * @property {string} endpoint 'oss-cn-qingdao.aliyuncs.com'
   * @property {string} ossFolder oss'jhx/user/image/wrq_zj/'
   * @property {OssFile[]} files 
   */
  /**
   * 
   *
   * @param {FileReq} [{
   *       bucket = 'xb-image',
   *       endpoint = 'oss-cn-qingdao.aliyuncs.com',
   *       ossPath,
   *       files
   *     }={}] 
   * @param {function(Array)} successCallback 
   * @param {Function} errorCallback 
   * @memberof System
   */
  uploadFiles(
    {
      bucket = 'xb-image',
      endpoint = 'oss-cn-qingdao.aliyuncs.com',
      ossPath = '',
      files
    } = {},
    successCallback,
    errorCallback
  ) {
    let req = {
      bucket,
      endpoint,
      ossPath,
      files
    }
    if (window.x_system)
      window.x_system.uploadFiles(
        ops => {
          console.log('ops:')
          console.dir(ops)
          let arr = []
          for (let item of req.files) {
            arr.push(`http://${req.bucket}.${req.endpoint}/${item.ossPath}`)
          }
          successCallback(arr)
        },
        errorCallback,
        JSON.stringify(req)
      )
    else if (window.wx) {
      console.error('')
      errorCallback()
    }
  }
  /**
   * 
   *
   * @param {any} width 
   * @param {any} height 
   * @param {function(Array)} successCallback ()
   * @param {Function} failureCallback 
   * @memberof System
   */
  takePhotoByArea(width, height, successCallback, failureCallback) {
    if (window.x_system)
      window.x_system.takePhotoByArea(
        successCallback,
        failureCallback,
        width,
        height
      )
    else if (window.wx) {
      console.warn('')
      window.wx.chooseImage({
        count: 1,
        sizeType: ['original', 'compressed'],
        sourceType: ['camera'],
        success: res => {
          successCallback(res.localIds)
        },
        fail: failureCallback,
        complete: () => {}
      })
    }
  }
  /**
   * 
   * @param {Number} lineId id()
   * @param {Number} direction 12()
   * @param {Number} unifiedId idid()
   * @param {Function} successCallback 
   * @param {Function} failureCallback 
   */
  openBusLineMap(
    lineId,
    direction,
    unifiedId,
    successCallback,
    failureCallback
  ) {
    if (window.x_system)
      window.x_system.openBusLineMap(
        successCallback,
        failureCallback,
        lineId,
        direction,
        unifiedId
      )
    else if (window.wx) {
      console.error('')
      failureCallback()
    }
    // console.log('')
  }
}



