import WX from '../weixin/wx'
import BarcodeScanner from '../cordova/android/barcodescanner'
import Location from '../cordova/android/locationXB'
import System from '../cordova/android/system'
import Thirdparty from '../cordova/android/thirdparty'
/**
 * JS
 *
 * @param {string} str 
 * @returns {Object}
 */
function payStrToObj(str) {
  let arr = str.split('&')
  let obj = {}
  for (let item of arr) {
    if (item.indexOf('=') >= 0) {
      let key = item.slice(0, item.indexOf('='))
      if (key === 'timeStamp') {
        key = 'timestamp'
      }
      if (key !== 'appId' && key !== '') {
        let value = item.slice(item.indexOf('=') + 1)
        obj[key] = value
      }
    }
  }
  return obj
}

export default class Common {
  constructor(plat) {
    // truecordovafalse
    this.plat = plat
    if (plat) {
      this.barcodeScanner = new BarcodeScanner()
      this.location = new Location()
      this.system = new System()
      this.thirdparty = new Thirdparty()
    } else if (!plat && window.wx) {
      this.wxobj = new WX()
    } else {
      return Object.create(null)
    }
  }
  /**
   * 
   *
   * @param {Function | function(Object)} successCallback needResult1{resultStr: ''}
   * @param {function(Object)} errorCallback 
   * @param {Function} [completeCallback=() => {}] , 
   * @param {Number} [needResult=0] , 01
   * @param {String[]} [scanType=['qrCode', 'barCode']] , ['qrCode', 'barCode'],
   * @memberof Common
   */
  scanQRCode(
    successCallback,
    errorCallback,
    completeCallback = () => {},
    needResult = 0,
    scanType = ['qrCode', 'barCode']
  ) {
    if (this.plat) {
      this.barcodeScanner.scanQRCode(successCallback, errorCallback)
    } else {
      this.wxobj.scanQRCode(
        needResult,
        scanType,
        successCallback,
        errorCallback,
        completeCallback
      )
    }
  }
  /**
   * @typedef {Object} WXLocationInfo
   * @property {string} latitude 90 ~ -90
   * @property {string} longitude 180 ~ -180
   * @property {string} speed /
   * @property {string} accuracy 
   */
  /**
   * @typedef {Object} CordovaLocationInfo
   * @property {string} latitude 90 ~ -90
   * @property {string} longitude 180 ~ -180
   * @property {string} pname 
   * @property {string} cname 
   * @property {string} aname 
   * @property {string} acode 
   * @property {string} poi 
   * @property {string} add 319
   */
  /**
   * 
   *
   * @param {function(WXLocationInfo) | function(CordovaLocationInfo)} successCallback ,
   * @param {function(Object)} errorCallback ,
   * @param {Function} [completeCallback=() => {}] 
   * @param {string} [type='wgs84'] wgs84gpsopenLocation'gcj02'
   * @memberof Common
   */
  getLocation(
    successCallback,
    errorCallback,
    completeCallback = () => {},
    type = 'wgs84'
  ) {
    if (this.plat) {
      // cordova
      this.location.isGPSOpen(
        isopen => {
          if (isopen) {
            this.location.getCurrentPosition(successCallback, errorCallback)
          } else {
            this.location.startService(
              () => {
                this.location.getCurrentPosition(successCallback, errorCallback)
              },
              err => {
                console.log('')
                errorCallback && errorCallback(err)
              }
            )
          }
        },
        err => {
          console.log('')
          errorCallback && errorCallback(err)
        }
      )
    } else {
      this.wxobj.getLocation(
        type,
        successCallback,
        errorCallback,
        completeCallback
      )
    }
  }
  /**
   * 
   *
   * @description cordova
   * @param {String} longitude 
   * @param {String} latitude 
   * @param {Function} successCallback 
   * @param {function(Object)} errorCallback 
   * @param {Function} [completeCallback=() => {}] , 
   * @param {String} [name=''] , 
   * @param {String} [address=''] , 
   * @param {Number} [scale=1] , ,,1~28
   * @param {String} [infoUrl=''] ,
   * @memberof Common
   */
  showLocationOnMap(
    longitude,
    latitude,
    successCallback,
    errorCallback,
    completeCallback = () => {},
    name = '',
    address = '',
    scale = 1,
    infoUrl = ''
  ) {
    if (this.plat) {
      this.location.openGaoDeAppByCurrentPosition(
        successCallback,
        errorCallback,
        longitude,
        latitude
      )
    } else {
      latitude = parseFloat(latitude)
      longitude = parseFloat(longitude)
      this.wxobj.openLocation(
        latitude,
        longitude,
        name,
        address,
        scale,
        infoUrl,
        successCallback,
        errorCallback,
        completeCallback
      )
    }
  }
  /**
   * ()
   *
   * @param {Number} count 
   * @param {function(String[])} successCallback cordova,"["file://xxx/xx.png","file://xxx/xxx.png"]",localIdlocalIdIDimgsrc
   * @param {function(Object)} errorCallback 
   * @param {Function} [completeCallback=() => {}] , 
   * @param {String[]} [sizeType=['original', 'compressed']] ,
   * @param {String[]} [sourceType=['album', 'camera']] ,
   * @param {Number} [width=undefined] cordova
   * @param {Number} [height=undefined] cordova
   * @memberof Common
   */
  chooseImage(
    count,
    successCallback,
    errorCallback,
    completeCallback = () => {},
    sizeType = ['original', 'compressed'],
    sourceType = ['album', 'camera'],
    width = undefined,
    height = undefined
  ) {
    if (this.plat) {
      this.system.photoPicker(
        count,
        width,
        height,
        res => {
          try {
            res = JSON.parse(res)
          } catch (err) {
            console.dir(err)
          } finally {
            successCallback && successCallback(res)
          }
        },
        errorCallback
      )
    } else {
      this.wxobj.chooseImage(
        count,
        sizeType,
        sourceType,
        res => {
          successCallback && successCallback(res.localIds)
        },
        errorCallback,
        completeCallback
      )
    }
  }
  /**
   * 
   *
   * @param {String} payStr 
   * @param {Function} successCallback 
   * @param {function(Object)} errorCallback 
   * @param {Function} [completeCallback=() => {}] , 
   * @param {string} payName cordovaalipay | wxpay
   * @memberof Common
   */
  pay(
    payStr,
    successCallback,
    errorCallback,
    completeCallback = () => {},
    payName
  ) {
    if (this.plat) {
      this.thirdparty.doPayment(payName, payStr, successCallback, errorCallback)
    } else {
      let obj = payStrToObj(payStr)
      let { timestamp, nonceStr, signType, paySign } = obj
      this.wxobj.chooseWXPay(
        parseInt(timestamp),
        nonceStr,
        obj['package'],
        signType,
        paySign,
        successCallback,
        errorCallback,
        completeCallback
      )
    }
  }
  /**
   * 
   *
   * @param {String} title 
   * @param {String} [content=''] cordova[shareType=(AppMessage|QQ|Weibo|QZone)]
   * @param {String} imgUrl 
   * @param {String} url JS
   * @param {Function} successCallback 
   * @param {function(Object)} errorCallback 
   * @param {Function} [cancelCallback=() => {}] , [shareType=(QQ|Weibo|QZone)]
   * @param {Function} [completeCallback=() => {}] , 
   * @param {String} [shareType=''] (Timeline-|AppMessage-|QQ-QQ|Weibo-|QZone-QQ)
   * @param {String} [dataType='link'] [shareType=AppMessage](music|video|link)
   * @param {String} [dataUrl=''] [shareType=AppMessage]
   * @memberof Common
   */
  share(
    title,
    content = '',
    imgUrl,
    url,
    successCallback,
    errorCallback,
    cancelCallback = () => {},
    completeCallback = () => {},
    shareType = '',
    dataType = 'link',
    dataUrl = ''
  ) {
    if (this.plat) {
      this.thirdparty.doOnekeyShare(
        title,
        content,
        imgUrl,
        url,
        successCallback,
        errorCallback
      )
    } else {
      switch (shareType) {
        case 'Timeline':
          this.wxobj.onMenuShareTimeline(
            title,
            url,
            imgUrl,
            successCallback,
            errorCallback,
            completeCallback
          )
          break
        case 'AppMessage':
          this.wxobj.onMenuShareAppMessage(
            title,
            content,
            url,
            imgUrl,
            dataType,
            dataUrl,
            successCallback,
            errorCallback,
            completeCallback
          )
          break
        case 'QQ':
          this.wxobj.onMenuShareQQ(
            title,
            content,
            url,
            imgUrl,
            successCallback,
            cancelCallback,
            errorCallback,
            completeCallback
          )
          break
        case 'Weibo':
          this.wxobj.onMenuShareWeibo(
            title,
            content,
            url,
            imgUrl,
            successCallback,
            cancelCallback,
            errorCallback,
            completeCallback
          )
          break
        case 'QZone':
          this.wxobj.onMenuShareQZone(
            title,
            content,
            url,
            imgUrl,
            successCallback,
            cancelCallback,
            errorCallback,
            completeCallback
          )
          break
        default:
          // 
          break
      }
    }
  }
}



