/**
 * obj
 * @param {string} uri 
 */
let getUrlParams = function(uri) {
  var url
  // 
  if (uri) url = decodeURIComponent(uri).substr(url.indexOf('?'))
  else url = decodeURIComponent(window.location.search) // url"?"
  var theParams = {}
  if (url.indexOf('?') !== -1) {
    var str = url.substr(1)
    var strs = str.split('&')
    for (var i = 0; i < strs.length; i++) {
      theParams[strs[i].split('=')[0]] = unescape(strs[i].split('=')[1])
    }
  }
  return theParams
}

/**
 * key
 * @param {String} key key
 * @param {String} uri 
 */
let getUrlParamByKey = function(key, uri) {
  let theParams = getUrlParams(uri)
  return theParams[key]
}

export { getUrlParams, getUrlParamByKey }



