// import ajaxAsync from '../../../ajax/ajaxAsync'
// import { getUrlParamByKey } from '../../../url/getUrlParams'

export default class Thirdparty {
  constructor() {
    if (window.x_thirdparty || window.wx) {
      console.log('ThirdpartyPluginReady')
    } else {
      return Object.create(null)
    }
  }
  /**
   * 
   *
   * @param {string} payName  alipay/wxpay
   * @param {string} payInfo 
   * @param {Function} successCallback 
   * @param {Function} errorCallback 
   * @memberof Thirdparty
   */
  doPayment(payName, payInfo, successCallback, errorCallback) {
    if (window.x_thirdparty)
      window.x_thirdparty.doPayment(
        successCallback,
        errorCallback,
        payName.toUpperCase(),
        payInfo
      )
    else if (window.wx) {
      if (payName === 'alipay') {
        console.error('')
        errorCallback()
      } else {
        let ss = payInfo.split('&')
        let payInfoObj = {
          success: successCallback,
          fail: errorCallback,
          cancel: res => {
            console.warn('')
            errorCallback(res)
          },
          complete: () => {}
        }
        for (let s of ss) {
          let info = s.split('=')
          payInfoObj[info[0]] = info[1]
        }
        window.wx.chooseWXPay(payInfoObj)
      }
    }
  }
  /**
   * 
   *
   * @param {function(string)} successCallback 
   *  JSON,  ["WXPAY","ALIPAY"]
   * @param {Function} errorCallback 
   * @memberof Thirdparty
   */
  getPaymentSupportList(successCallback, errorCallback) {
    if (window.x_thirdparty)
      window.x_thirdparty.getPaymentSupportList(successCallback, errorCallback)
    else if (window.wx) {
      successCallback(['WXPAY'])
    }
  }
  /**
   * 
   *
   * @param {string} title title
   * @param {string} content 
   * @param {string} picUrl url
   * @param {string} url url
   * @param {Function} successCallback 
   * @param {Function} errorCallback 
   * @memberof Thirdparty
   */
  doOnekeyShare(title, content, picUrl, url, successCallback, errorCallback) {
    if (window.x_thirdparty)
      window.x_thirdparty.doOnekeyShare(
        successCallback,
        errorCallback,
        title,
        content,
        picUrl,
        url
      )
    else if (window.wx) {
      //QQ
      window.wx.updateAppMessageShareData({
        title: title, // 
        desc: content, // 
        link: url, // JS
        imgUrl: picUrl, // 
        success: successCallback,
        fail: errorCallback,
        complete: () => {}
      })
      //QQ
      window.wx.updateTimelineShareData({
        title: title, // 
        link: url, // JS
        imgUrl: picUrl, // 
        success: successCallback,
        fail: errorCallback,
        complete: () => {}
      })
    }
  }
  /**
   * 
   *
   * @param {string} thirdPartyName  (QQ,Wechat,SinaWeibo)
   * @param {function(JSON)} successCallback 
   * 
   *      {
   *          "openid": "o6_bmjrPTlm6_2sgVt7hMZOPfL2M","nickname": "Band","sex": 1,"language": "zh_CN","city": "","province": "","country": "","province":[],
   *          "headimgurl":  "http://wx.qlogo.cn/mmopen/g3MonUZtNHkdmzicIlibx6iaFqAc56vxLSUfpb6n5WKSYVY0ChQKkiaJSgQ1dZuTOgvLLrhJbERQQ4eMsv84eavHiaiceqxibJxCfHe/0",
   *           "unionid": " o6_bmasdasdsad6_2sgVt7hMZOPfL"
   *       }
   * @param {Function} errorCallback 
   * @memberof Thirdparty
   */
  doAuthorizedLogin(thirdPartyName, successCallback, errorCallback) {
    if (window.x_thirdparty)
      window.x_thirdparty.doAuthorizedLogin(
        successCallback,
        errorCallback,
        thirdPartyName
      )
    else if (window.wx) {
      if (thirdPartyName === 'Wechat') {
        if (window.location.href.includes('code')) {
          // ajaxAsync('/wx/user/login', {
          //   CODE: getUrlParamByKey('code'),
          //   APPID: window.appid
          // })
          console.warn(
            '(/wx/user/login)'
          )
          successCallback()
        } else {
          let url = encodeURIComponent(window.location.href)
          window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${
            window.appid
          }&redirect_uri=${url}&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect`
        }
      } else {
        console.error('')
        errorCallback()
      }
    }
  }
  /**
   * 
   *
   * @param {string} eventId ID
   * @param {Object} eventInfo  { "": ""}
   * @param {number} count 
   * @param {Function} successCallback 
   * @param {Function} errorCallback 
   * @memberof Thirdparty
   */
  doRecordEvent(eventId, eventInfo, count, successCallback, errorCallback) {
    if (window.x_thirdparty)
      window.x_thirdparty.doRecordEvent(
        successCallback,
        errorCallback,
        eventId,
        eventInfo,
        count
      )
    else if (window.wx) {
      console.error('')
      errorCallback()
    }
  }
  /**
   * 
   *
   * @param {number} lineid id
   * @param {number} [direction=2] ,2
   * @param {string} longitude 
   * @param {string} latitude 
   * @param {Function} successCallback 
   * @param {Function} errorCallback 
   * @memberof Thirdparty
   */
  doMapBusLineShow(
    lineid,
    direction = 2,
    longitude,
    latitude,
    successCallback,
    errorCallback
  ) {
    if (window.x_thirdparty)
      window.x_thirdparty.doMapBusLineShow(
        successCallback,
        errorCallback,
        lineid,
        direction,
        longitude,
        latitude
      )
    else if (window.wx) {
      console.error('')
      errorCallback()
    }
  }
  /**
   * 
   *
   * @param {Function} successCallback 
   * @param {Function} failureCallback 
   * @param {String} adChannelId ID
   * @memberof Thirdparty
   */
  getAdInfo(successCallback, failureCallback, adChannelId) {
    if (window.x_thirdparty)
      window.x_thirdparty.getAdInfo(
        successCallback,
        failureCallback,
        adChannelId
      )
    else if (window.wx) {
      console.error('')
      failureCallback()
    }
  }
}



